import Vue from 'vue'
import Router from 'vue-router'

import layout from '../layout'

Vue.use(Router)

const router = new Router({
  linkExactActiveClass: 'active',
  scrollBehavior: () => ({ y: 0 }),
  mode: 'history',
  base: '/panel/',
  routes: [
    {
      path: '/',
      component: layout,
      children: [
        {
          path: '',
          name: 'dashboard',
          component: () => import('@/pages/users/users_list'),
          meta: {
            requiresAuth: true
          }
        }
      ]
    },
    {
      path: '/users',
      component: layout,
      children: [
        {
          path: 'user-list',
          name: 'user-list',
          component: () => import('@/pages/users/users_list')
        }
      ]
    },
    {
      path: '/report',
      component: layout,
      children: [
        {
          path: 'remainingStock',
          name: 'remainingStock',
          component: () => import('@/pages/report/remaining_stock')
        }
      ]
    },
    {
      path: '/records',
      component: layout,
      children: [
        {
          path: 'records-list/:id',
          name: 'records-list',
          component: () => import('@/pages/records/records_list'),
          props: true
        },
        {
          path: 'records-detail/:id',
          name: 'records-detail',
          component: () => import('@/pages/records/records_detail'),
          props: true
        },
      ]
    },
    // {
    //   path: '/basic-ui',
    //   component: layout,
    //   children: [
    //     {
    //       path: 'buttons',
    //       name: 'buttons',
    //       component: () => import('@/pages/basic-ui/buttons')
    //     },
    //     {
    //       path: 'dropdowns',
    //       name: 'dropdowns',
    //       component: () => import('@/pages/basic-ui/dropdowns')
    //     }
    //   ]
    // },
    // {
    //   path: '/charts',
    //   component: layout,
    //   children: [
    //     {
    //       path: 'chartjs',
    //       name: 'chartjs',
    //       component: () => import('@/pages/charts/chartjs')
    //     },
    //   ]
    // },
    // {
    //   path: '/tables',
    //   component: layout,
    //   children: [
    //     {
    //       path: 'basic-tables',
    //       name: 'basic-tables',
    //       component: () => import('@/pages/tables/basic-tables')
    //     }
    //   ]
    // },
    {
      path: '/auth-pages',
      component: {
        render (c) { return c('router-view') }
      },
      children: [
        {
          path: 'login',
          name: 'login',
          component: () => import('@/pages/samples/auth-pages/login')
        },
        {
          path: 'register',
          name: 'register',
          component: () => import('@/pages/samples/auth-pages/register')
        },
        {
          path: 'create-shop',
          name: 'create-shop',
          component: () => import('@/pages/samples/auth-pages/create_shop')
        }
      ]
    },
    {
      path: '/error-pages',
      component: {
        render (c) { return c('router-view') }
      },
      children: [
        {
          path: 'error-404',
          name: 'error-404',
          component: () => import('@/pages/samples/error-pages/error-404')
        },
        {
          path: 'error-500',
          name: 'error-500',
          component: () => import('@/pages/samples/error-pages/error-500')
        }
      ]
    },
    {
      path: '/icons',
      component: layout,
      children: [
        {
          path: 'mdi-icons',
          name: 'mdi-icons',
          component: () => import('@/pages/icons/mdi-icons')
        }
      ]
    },
    {
      path: '*',
      redirect: '/error-404',
      component: {
        render (c) { return c('router-view') }
      },
      children: [
        {
          path: 'error-404',
          component: () => import('@/pages/samples/error-pages/error-404')
        }
      ]
    }
  ],

})

router.beforeEach((to, from, next) => {
  // eslint-disable-next-line no-constant-condition
  if (to.matched.some(record => record.meta.requiresAuth) && (localStorage.token == null || localStorage.token =='')) {
    // this route requires auth, check if logged in
    // if not, redirect to login page.
    next({name: 'login'})
  } else {
    next() // does not require auth, make sure to always call next()!
  }
})

export default router